import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

//const axios = require('axios');
//const apiUrl = '';

export default new Vuex.Store({
    state: {
        IsHomePage: true,
        IsMobile: false
    },
    getters: {
        GetIsHomePage(state){
            return state.IsHomePage;
        },
        GetIsMobile(state){
            return state.IsMobile;
        }
    },
    mutations: {
        ToggleIsHomePage(state, status){
            state.IsHomePage = status;
        }
    },
    actions: {
        _ToggleIsHomePage({commit}, status){
            commit('ToggToggleIsHomePageleNav', status);
        }
    }
});